import {policyShow} from '@/api/personnel-policy'
import baseLoading from 'components/base-loading.vue';

export default {
  name: 'personnelPolicy',
  components: {
    baseLoading,
  },
  data() {
    return {
      policyTitleList: [],
      isLoadingData:true,
    }
  },
  mounted () {
    let _this = this;
    policyShow({"page": 1, "pagesize": 100}).then(response => {
      _this.policyTitleList = response.data.rows;
      _this.isLoadingData=false;
    }).catch(error => {
      _this.showLoading = false;
      console.log(error);
      _this.isLoadingData=false;
    })
  },
  created() {
  },
  methods: {
    linkTarget(item){
      window.location.href = item.hrFileUrl;
    }
  },

}



